exports.push([module.id, "#krabby_patty_document .logo[data-v-48194d9b]{text-align:center}#krabby_patty_document .logo img[data-v-48194d9b]{height:260px;width:auto}#krabby_patty_document .logo h1[data-v-48194d9b]{font-size:60px}.layout[data-v-48194d9b]{border:1px solid #d7dde4;background:#f5f7f9;position:relative;border-radius:4px;overflow:hidden}.layout-logo[data-v-48194d9b]{width:100px;height:30px;background:#f5f7f9;border-radius:3px;float:left;position:relative;top:15px;left:20px}.common-tree[data-v-48194d9b]{min-height:252px}.code pre[data-v-48194d9b]{background:url(" + escape(require("../../img/code-block-bg.png")) + ") #f5f6f7;width:775px;border-left:3px solid #000;line-height:24px;font-size:16px}.catalog[data-v-48194d9b]{padding:16px}", "", {"version":3,"sources":["d:/project/CherryBoomBoom.github.io/src/components/krabby-patty/src/components/krabby-patty/document.vue"],"names":[],"mappings":"AAiLA,8CACA,iBAAA,CACA,AACA,kDACA,aAAA,AACA,UAAA,CACA,AACA,iDACA,cAAA,CACA,AACA,yBACA,yBAAA,AACA,mBAAA,AACA,kBAAA,AACA,kBAAA,AACA,eAAA,CACA,AACA,8BACA,YAAA,AACA,YAAA,AACA,mBAAA,AACA,kBAAA,AACA,WAAA,AACA,kBAAA,AACA,SAAA,AACA,SAAA,CACA,AACA,8BACA,gBAAA,CACA,AACA,2BACA,iDAAA,AACA,YAAA,AACA,2BAAA,AACA,iBAAA,AACA,cAAA,CACA,AACA,0BACA,YAAA,CACA","file":"document.vue?vue&type=style&index=0&id=48194d9b&scoped=true&lang=css&","sourcesContent":["<template>\r\n  <div id=\"krabby_patty_document\">\r\n    <div>\r\n      <div class=\"logo\">\r\n        <img src=\"../../img/humber.svg\" />\r\n        <h1>krabby patty</h1>\r\n        <h3>蟹黄堡web框架原型机</h3>\r\n      </div>\r\n    </div>\r\n    <Divider />\r\n\r\n    <Row>\r\n      <Col span=\"18\">\r\n        <div style=\"border-right:1px solid #e8eaec\">\r\n          <h1 id=\"preface\">写在前面</h1>\r\n          <p>- 这是我自己开发的框架.在此之前我一直使用的是koa/egg框架进行开发,但是过程中发现有很多不尽我意的地方,为了能够更顺手的开发web程序,我决定自己开发</p>\r\n          <p>\r\n            - 名字的来源是两个朋友的名字联合,因此我称它为\r\n            <b>蟹黄堡</b>\r\n          </p>\r\n          <p>\r\n            - 开发的中心思想是完全符合\r\n            <b>我</b>的开发习惯,以及尽可能的\r\n            <b>模块化</b>和附带\r\n            <b>智能提示</b>\r\n          </p>\r\n          <h1 id=\"advantage\">优势</h1>\r\n          <p># 组件\\请求\\工具Load在上下文中</p>\r\n          <p># 可以自定义loader及typings</p>\r\n          <p># 非常夸张的深层嵌套洋葱圈模型</p>\r\n          <p># 企图可拆分微服务模块</p>\r\n          <p># 自动重启以及更多的开放性api</p>\r\n          <p># 以约束定义规则</p>\r\n          <h1 id=\"init\">安装及快速开始</h1>\r\n          <div class=\"code\">\r\n            <pre>{{code.init}}</pre>\r\n          </div>\r\n          <h1 id=\"tree\">目录树</h1>\r\n          <h2 id=\"common_tree\">标准目录</h2>\r\n          <p>常用的目录结构</p>\r\n          <div class=\"common-tree\">\r\n            <Tree :data=\"fileTree_1\"></Tree>\r\n          </div>\r\n          <h2 id=\"module_tree\">子模块目录</h2>\r\n          <p>业务需要有嵌套模块的目录结构,此时中间件也是嵌套的</p>\r\n          <div>\r\n            <Tree :data=\"fileTree_2\"></Tree>\r\n          </div>\r\n          <h1 id=\"enter\">入口文件</h1>\r\n          <p>项目从这里开始,并启动一个蟹黄堡实例</p>\r\n          <p>这里默认引入./config/base.config,你也可以传入一个合法的参数替换它</p>\r\n          <div class=\"code\">\r\n            <pre>{{code.app}}</pre>\r\n          </div>\r\n          <h1 id=\"module\">Module</h1>\r\n          <p>这里是面包片,功能都在这里组合</p>\r\n          <div class=\"code\">\r\n            <pre>{{code.module}}</pre>\r\n          </div>\r\n          <h1 id=\"controller\">Controller</h1>\r\n          <p>这里是路由和功能组合文件</p>\r\n          <p>\r\n            我是用\r\n            <b>[new Symbol()]</b>作为\r\n            <b>Function</b>名称.因为我认为这里的索引是路由的地址,方法不应该被其他形式的调用\r\n          </p>\r\n          <div class=\"code\">\r\n            <pre>{{code.controller}}</pre>\r\n          </div>\r\n\r\n          <h1 id=\"service\">Service</h1>\r\n          <p>这里是业务功能模块</p>\r\n          <p>\r\n            这里可以在上下文拿到请求的\r\n            <b>body</b>,\r\n            <b>file</b>,\r\n            <b>query</b>,\r\n            <b>header</b>\r\n          </p>\r\n          <div class=\"code\">\r\n            <pre>{{code.service}}</pre>\r\n          </div>\r\n\r\n          <h1 id=\"middleware\">Middleware</h1>\r\n          <p>这里是请求中间件</p>\r\n          <p>中间件的加载顺序为底层base.module -> controller -> router</p>\r\n          <p>多模块的中间件的加载顺序base.module -> 子模块base.module,往下与基本顺序对齐</p>\r\n          <h2 id=\"module_middleware\">module级别中间件</h2>\r\n          <div class=\"code\">\r\n            <pre>{{code.middleware.module}}</pre>\r\n          </div>\r\n          <h2 id=\"controller_middleware\">controller及router级别中间件</h2>\r\n          <div class=\"code\">\r\n            <pre>{{code.middleware.controller}}</pre>\r\n          </div>\r\n        </div>\r\n\r\n        <h1 id=\"model\">Model</h1>\r\n        <p>这里是数据库实体</p>\r\n        <p>目前只支持mongoose的类型提醒</p>\r\n        <div class=\"code\">\r\n          <pre>{{code.model}}</pre>\r\n        </div>\r\n        <p>需要在module中定义,这里预设了mongooseModel,mongooseTsHelper两个专用与mongoose的方法</p>\r\n        <div class=\"code\">\r\n          <pre>{{code.modelLoad}}</pre>\r\n        </div>\r\n        <div id=\"ingredient\">\r\n        <h1>Ingredient</h1>\r\n        <p>这里是装载器，你可以把任何变量装载在上下文中并获得代码提示,子模块同样可以获得父类的成员</p>\r\n        <div class=\"code\">\r\n          <pre>{{code.modelLoad}}</pre>\r\n        </div>\r\n        </div>\r\n\r\n        <h1 id=\"config\">Config</h1>\r\n        <p>这里是配置,默认配置为base.config,当你启动dev.config时,dev.config会替代base.config相同Key的字段</p>\r\n        <div class=\"code\">\r\n          <pre>{{code.config}}</pre>\r\n        </div>\r\n      </Col>\r\n      <Col span=\"6\">\r\n        <div class=\"catalog\">\r\n          <Anchor show-ink :offset-top=\"76\">\r\n            <AnchorLink href=\"#preface\" title=\"写在前面\" />\r\n            <AnchorLink href=\"#advantage\" title=\"优势\" />\r\n            <AnchorLink href=\"#init\" title=\"安装及快速开始\" />\r\n            <AnchorLink href=\"#tree\" title=\"目录树\">\r\n              <AnchorLink href=\"#common_tree\" title=\"标准目录\" />\r\n              <AnchorLink href=\"#module_tree\" title=\"子模块目录\" />\r\n            </AnchorLink>\r\n            <AnchorLink href=\"#enter\" title=\"入口文件\" />\r\n            <AnchorLink href=\"#module\" title=\"Module\" />\r\n            <AnchorLink href=\"#controller\" title=\"Controller\" />\r\n            <AnchorLink href=\"#service\" title=\"Service\" />\r\n            <AnchorLink href=\"#middleware\" title=\"Middleware\">\r\n              <AnchorLink href=\"#module_middleware\" title=\"module级别中间件\" />\r\n              <AnchorLink href=\"#controller_middleware\" title=\"controller及router级别中间件\" />\r\n            </AnchorLink>\r\n            <AnchorLink href=\"#model\" title=\"Model\" />\r\n            <AnchorLink href=\"#ingredient\" title=\"Ingredient\" />\r\n            <AnchorLink href=\"#config\" title=\"Config\" />\r\n          </Anchor>\r\n        </div>\r\n      </Col>\r\n    </Row>\r\n  </div>\r\n</template>\r\n\r\n<script lang=\"ts\">\r\nimport { IVue, IComponent, IWatch } from '../../interface/vue'\r\nimport code from '../../code'\r\n\r\n@IComponent({ components: {} })\r\nexport default class Document extends IVue {\r\n  code = code\r\n  fileTree_1 = [\r\n    {\r\n      title: '├module_1',\r\n      expand: true,\r\n      children: [{ title: '├controller' }, { title: '├service' }, { title: '├module_1.module.ts' }]\r\n    },\r\n    {\r\n      title: '├module_2',\r\n      expand: true,\r\n      children: [{ title: '├controller' }, { title: '├service' }, { title: '├module_2.module.ts' }]\r\n    },\r\n    { title: '├base.module.ts' },\r\n    { title: '├package.json' }\r\n  ]\r\n  fileTree_2 = [{ title: '├controller' }, { title: '├service' }, { title: '├base.module.ts' }, { title: '├package.json' }]\r\n  mounted() {\r\n    console.log(1)\r\n  }\r\n}\r\n</script>\r\n<style scoped>\r\n#krabby_patty_document .logo {\r\n  text-align: center;\r\n}\r\n#krabby_patty_document .logo img {\r\n  height: 260px;\r\n  width: auto;\r\n}\r\n#krabby_patty_document .logo h1 {\r\n  font-size: 60px;\r\n}\r\n.layout {\r\n  border: 1px solid #d7dde4;\r\n  background: #f5f7f9;\r\n  position: relative;\r\n  border-radius: 4px;\r\n  overflow: hidden;\r\n}\r\n.layout-logo {\r\n  width: 100px;\r\n  height: 30px;\r\n  background: #f5f7f9;\r\n  border-radius: 3px;\r\n  float: left;\r\n  position: relative;\r\n  top: 15px;\r\n  left: 20px;\r\n}\r\n.common-tree {\r\n  min-height: 252px;\r\n}\r\n.code pre {\r\n  background: url(../../img/code-block-bg.png) #f5f6f7;\r\n  width: 775px;\r\n  border-left: 3px solid black;\r\n  line-height: 24px;\r\n  font-size: 16px;\r\n}\r\n.catalog {\r\n  padding: 16px;\r\n}\r\n</style>"],"sourceRoot":""}]);

